<template>
  <div class="wrapper">
    <div class="phones">
      <UserOne
        :info_msg="info_msg"
        @msg="getMsg"
        @modalOpen="isModalView = !isModalView"
        @modalState="this.modalState = true"
      />
      <UserTwo
        :info_msg="info_msg"
        @msg_two="sendMsgTwo"
        @modalOpen="isModalView = !isModalView"
        @modalState="this.modalState = false"
      />
      <Modal
        :isModalView="isModalView"
        @requireModal="isModalView = requireModal"
        v-show="isModalView"
        @sendData="getData"
        :modalState="this.modalState"
      />
    </div>
  </div>
</template>

<script>
import UserOne from "@/components/UserOne.vue";
import UserTwo from "@/components/UserTwo.vue";
import Modal from "@/components/Modal.vue";

export default {
  components: { UserOne, UserTwo, Modal },
  data() {
    return {
      info_msg: [],
      isModalView: false,
      modalState: '',
    };
  },
  methods: {
    sendMsgTwo(msg_two) {
      this.info_msg.push({
        id: 1,
        time: new Date().toTimeString().replace(/:[0-9]{2,2} .*/, ""),
        content: msg_two,
        img: "",
      });
    },
    getMsg(msg) {
      this.info_msg.push({
        id: 2,
        time: new Date().toTimeString().replace(/:[0-9]{2,2} .*/, ""),
        content: msg,
        img: "",
      });
    },
    getData(data) {
      this.info_msg.push({
        id: this.modalState == true ? 2 : 1,
        time: new Date().toTimeString().replace(/:[0-9]{2,2} .*/, ""),
        content: data.comment,
        img: data.url,
      });
    },
    getLSList() {
      const localList = localStorage.list
      if(localList) {
        this.info_msg = JSON.parse(localList)
      }
    },
  },
  created() {
    this.getLSList();
  },
  watch: {
    info_msg: {
      handler() {
        localStorage.list = JSON.stringify(this.info_msg)
      },
      deep: true,
    },
  },
};
</script>