<template>
  <Transition name="modal">
    <div class="modal" @click="closeModal">
      <div class="modal__block" @click.stop="">
        <h2 class="modal__block-title">Отправить картинку</h2>
        <div class="modal__block-inputs">
          <label>
            <span>URL</span>
            <input type="text" placeholder="URL" v-model="data.url"/>
          </label>
          <label>
            <span>Комментарий</span>
            <input type="text" placeholder="Комментарий" v-model="data.comment"/>
          </label>
        </div>
        <div class="modal__block-btns">
          <button class="modal__block-btn del" @click="closeModal">
            Отмена
          </button>
          <button class="modal__block-btn send" @click="sendData">ОТПРАВИТЬ</button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
export default {
  data() {
    return {
      data: {
        url: "",
        comment: "",
      },
      requireModal: "",
    };
  },
  methods: {
    closeModal() {
      this.requireModal = this.isModalView;
      this.requireModal = !this.requireModal;
      this.$emit("requireModal", this.requireModal);
      this.data.url = this.data.comment = '';
      this.modalState = 0;
    },
    sendData() {
      if (this.data.url.trim() != '' && this.data.comment.trim() != '') {    
        this.$emit("sendData", this.data)
        this.closeModal()
        this.data.url = this.data.comment = '';
      }
    }
  },
  props: {
    isModalView: {
      type: Boolean,
      required: true,
    },
    modalState: {
      type: Boolean,
    }
  },
};
</script>