<template>
  <div class="phone">
    <nav class="phone__nav">
      <img src="@/assets/images/user_one.svg" alt="" />
      <div class="phone__nav-info">
        <span class="phone__nav-info-name">Александр</span>
        <span class="phone__nav-info-state">Онлайн</span>
      </div>
    </nav>
    <div class="phone__main">
      <div
        class="phone__main-msg"
        :class="{ user_1: msg.id == 2 }"
        v-for="(msg, i) in info_msg"
        :key="i"
      >
        <span class="phone__main-msg-time" v-show="msg.id == 2">{{
          msg.time
        }}</span>
        <div class="phone__main-msg-msg" :class="{ user_1: msg.id == 2 }">
          <img :src="msg.img" alt="" />
          <span>{{ msg.content }}</span>
        </div>
        <span class="phone__main-msg-time" v-show="msg.id == 1">{{
          msg.time
        }}</span>
      </div>
    </div>
    <div class="phone__footer">
      <input type="text" placeholder="Написать сообщение..." v-model="msg" />
      <button @click="modalOpen">
        <img
          src="@/assets/images/cam.svg"
          alt=""
          v-show="this.msg.length == 0"
        />
      </button>
      <button @click="sendMsg">
        <img
          src="@/assets/images/send.svg"
          alt=""
          v-show="this.msg.length != 0"
        />
      </button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: "",
      isPrint: false,
      modalState: 0,
    };
  },
  props: {
    info_msg: {
      type: Array,
      required: true,
    },
  },
  methods: {
    sendMsg() {
      if (this.msg.trim() != "") {
        this.$emit("msg", this.msg);
        this.msg = "";
      }
    },
    modalOpen() {
      this.$emit("modalOpen");
      this.$emit("modalState", this.modalState);
    },
  },
};
</script>